import document from 'global/document';
import axios from '~global-js/helpers/axios';
import { Promise, all } from 'rsvp';
import es6Promise from 'es6-promise';

export default class Svg4EveryBody {
  constructor() {
    this.state = {
      list: {},
      data: {},
      updating: false,
    };
  }

  init = () => {
    es6Promise.polyfill();

    if (this.polyDetect()) {
      this.polyFill();

      setInterval(() => {
        this.polyFill();
      }, 5000);
    }
  };

  polyDetect = () => {
    const userAgent = navigator.userAgent;
    const olderIEUA = /\bMSIE [1-8]\.0\b/;
    const newerIeua = /\bTrident\/[567]\b|\bMSIE (?:9|10)\.0\b/;
    const webkitUa = /\bAppleWebKit\/(\d+)\b/;
    const olderEdgeUa = /\bEdge\/12\.(\d+)\b/;
    const edgeUa = /\bEdge\/.(\d+)\b/;
    const inIFrame = window.top !== window.self;
    const huaWeiBrowser = /\bHUAWEI\b/;

    return olderIEUA.test(userAgent)
      || newerIeua.test(userAgent)
      || (userAgent.match(olderEdgeUa) || [])[1] < 10547
      || (userAgent.match(webkitUa) || [])[1] < 537
      || (edgeUa.test(userAgent) && inIFrame)
      || ((userAgent.match(webkitUa) || [])[1] < 538 && huaWeiBrowser.test(userAgent));
  };

  polyFill = () => {
    const files = document.querySelectorAll('use');

    if (!files) {
      return false;
    }

    [].slice.call(files).map((file) => {
      const fullPosition = file.getAttribute('xlink:href');
      const url = fullPosition.split('#')[0];
      const svgName = fullPosition.split('#')[1];

      if (url && Object.keys(this.state.list).indexOf(url) === -1) {
        this.state.list[url] = [svgName];
      } else if (url && svgName && this.state.list[url].indexOf(svgName) === -1) {
        this.state.list[url].push(svgName);
      }

      return true;
    });

    if (!this.state.updating && Object.keys(this.state.list).length > 0) {
      this.updateState();
    }

    return true;
  };

  updateState = () => {
    const fileNames = Object.keys(this.state.list);
    const svgLoad = [];

    this.state.updating = true;

    fileNames.map((svg) => {
      const promise = new Promise((resolve) => {
        axios.get(svg, {
          timeout: 2000,
        })
          .then((response) => {
            const container = document.implementation.createHTMLDocument('');
            container.body.innerHTML = response.data;

            if (!this.state.data[svg]) {
              this.state.data[svg] = {};
            }

            this.state.list[svg].map((id) => {
              const element = container.getElementById(id);

              if (element && !this.state.data[svg][id]) {
                this.state.data[svg][id] = element;
              }

              return true;
            });
            resolve();
          })
          .catch(() => {
            resolve();
          });
      });
      svgLoad.push(promise);

      return true;
    });

    all(svgLoad).then(() => {
      this.updateDom();
    });
  };

  updateDom = () => {
    const nodes = document.querySelectorAll('use');
    [].slice.call(nodes).map((node) => {
      const position = node.getAttribute('xlink:href');

      if (!position) {
        return false;
      }
      const url = position.split('#')[0];
      const name = position.split('#')[1];

      const data = this.state.data[url] ? this.state.data[url][name] : null;
      if (data) {
        this.embed(node.parentNode, node, data);
      }

      return true;
    });

    this.state = {
      list: {},
      data: {},
      updating: false,
    };
  };

  embed = (parent, svg, target) => {
    if (target) {
      const fragment = document.createDocumentFragment();
      const viewBox = !svg.hasAttribute('viewBox') && target.getAttribute('viewBox');

      if (viewBox) {
        parent.setAttribute('viewBox', viewBox);
      }

      for (let clone = target.cloneNode(!0); clone.childNodes.length;) {
        fragment.appendChild(clone.firstChild);
      }
      parent.removeChild(svg);
      parent.appendChild(fragment);
    }
  };
}



